'use client';

import { useState } from 'react';

type FundDetail = {
  code: string;
  name: string;
  type: string;
  netWorth: number | null;
  netWorthDate: string | null;
  dayGrowth: number;
  lastMonthGrowth?: number;
  lastThreeMonthsGrowth?: number;
  lastYearGrowth?: number;
};

export default function FundSearchPanel() {
  const [code, setCode] = useState('');
  const [detail, setDetail] = useState<FundDetail | null>(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  async function search() {
    if (!code.trim()) return;
    try {
      setLoading(true);
      setError(null);
      setDetail(null);
      const res = await fetch(`/api/funds/${encodeURIComponent(code.trim())}`, { cache: 'no-store' });
      const data = await res.json();
      const info = Array.isArray(data?.data) ? data.data[0] : null;
      if (!info) throw new Error('未找到基金信息');
      setDetail({
        code: info.code,
        name: info.name,
        type: info.type,
        netWorth: info.netWorth ?? null,
        netWorthDate: info.netWorthDate ?? null,
        dayGrowth: Number(info.dayGrowth ?? 0),
        lastMonthGrowth: info.lastMonthGrowth != null ? Number(info.lastMonthGrowth) : undefined,
        lastThreeMonthsGrowth: info.lastThreeMonthsGrowth != null ? Number(info.lastThreeMonthsGrowth) : undefined,
        lastYearGrowth: info.lastYearGrowth != null ? Number(info.lastYearGrowth) : undefined,
      });
    } catch (e: any) {
      setError(e?.message || '查询失败');
    } finally {
      setLoading(false);
    }
  }

  return (
    <div>
      <div className="px-6 py-4 border-b flex items-center gap-3">
        <div className="text-lg font-semibold">基金代码查询</div>
        <div className="flex items-center gap-2 ml-auto">
          <input
            className="border rounded px-3 py-1 text-sm"
            placeholder="如 110022"
            value={code}
            onChange={(e) => setCode(e.target.value)}
          />
          <button onClick={search} className="px-3 py-1 text-sm rounded bg-blue-600 text-white">查询</button>
        </div>
      </div>
      {error && <div className="px-6 pt-2 text-sm text-red-600">{error}</div>}
      <div className="overflow-x-auto">
        <table className="min-w-full">
          <thead className="bg-gray-50">
            <tr>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500">代码</th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500">名称</th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500">类型</th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500">净值</th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500">净值日期</th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500">日涨跌</th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500">近一月</th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500">近三月</th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500">近一年</th>
            </tr>
          </thead>
          <tbody>
            {loading && (
              <tr><td className="px-6 py-8 text-center text-gray-500" colSpan={9}>加载中...</td></tr>
            )}
            {!loading && detail && (
              <tr className="border-t">
                <td className="px-6 py-3">{detail.code}</td>
                <td className="px-6 py-3">{detail.name}</td>
                <td className="px-6 py-3">{detail.type}</td>
                <td className="px-6 py-3">{detail.netWorth ?? '-'}</td>
                <td className="px-6 py-3 text-xs text-gray-500">{detail.netWorthDate ?? '-'}</td>
                <td className={`px-6 py-3 ${Number(detail.dayGrowth) >= 0 ? 'text-green-600' : 'text-red-600'}`}>{detail.dayGrowth}%</td>
                <td className="px-6 py-3">{detail.lastMonthGrowth != null ? `${detail.lastMonthGrowth}%` : '-'}</td>
                <td className="px-6 py-3">{detail.lastThreeMonthsGrowth != null ? `${detail.lastThreeMonthsGrowth}%` : '-'}</td>
                <td className="px-6 py-3">{detail.lastYearGrowth != null ? `${detail.lastYearGrowth}%` : '-'}</td>
              </tr>
            )}
            {!loading && !detail && (
              <tr><td className="px-6 py-8 text-center text-gray-500" colSpan={9}>请输入基金代码查询</td></tr>
            )}
          </tbody>
        </table>
      </div>
    </div>
  );
}

